<template>
    <Modal v-model="modal_preView" :mask-closable="false" @on-visible-change="onVisibleChange" width="798" :footerHide="true">
        <h2>-</h2>
        <Row style="margin-top: 16px; margin-bottom: 16px">
            <Col span="12">
            <div class="preview-plan">
                <div class="preview-iframe">
                    <iframe :src="qrcodeAdderss" width="100%" height="100%" frameborder="0"></iframe>
                </div>
            </div>
            <div style="text-align: center; margin-top: 6px;">
                <ButtonGroup shape="circle">
                    <Button ><Icon type="chevron-left"></Icon></Button>
                    <Button ><Icon type="chevron-right"></Icon></Button>
                </ButtonGroup>
            </div>
            </Col>
            <Col span="12">
            <div>
                <h3></h3>
                <div>
                    <Input style="margin-top: 6px" v-model="theme.title" :readonly="!getStatusEdit()" placeholder="..." ></Input>
                </div>
                <br>
                <div>
                    <Input v-model="theme.description" type="textarea"  :readonly="!getStatusEdit()" :autosize="{ minRows: 3, maxRows: 4 }" placeholder="..." ></Input>
                </div>
                <br>

                <div style="width: 100%; margin-top: 6px ">
                    <Row>
                        <Col style="text-align: center" span="12">
                            <h3></h3>
                            <qrcode :options="{ size: 164 }" :value="qrcodeAdderss" ></qrcode>
                        </Col>
                        <Col style="text-align: center" span="12">
                            <h3>H5</h3>
                            <div style="margin-top: 14px; ">
                               <div class="theme-bg">
                                   <img  v-if="theme.themebg" :src="baseHost + theme.themebg" width="134" height="134" alt="">
                                   <div v-if="getStatusEdit()" class="setting-change-bg">
                                       <Upload multiple
                                               :action="uploadUrl + userinfo._id"
                                               name="uploadFile"
                                               :show-upload-list="false"
                                               :max-size="2048"
                                               :on-success="handleSuccess"
                                               :on-format-error="handleFormatError"
                                               :on-exceeded-size="handleMaxSize">
                                           <Button type="primary" icon="ios-cloud-upload"></Button>
                                       </Upload>
                                   </div>
                               </div>
                            </div>
                        </Col>
                    </Row>
                    <div>
                       <Row>
                           <Col span="18">
                               <Input id="themeurlid" type="textarea" :autosize="{ minRows: 2, maxRows: 2 }"
                                      :value="qrcodeAdderss" size="large" readonly placeholder="url"></Input>
                           </Col>
                           <Col span="6"><Button @click="onCopyEvent" style="line-height: 3rem;"></Button></Col>
                       </Row>
                    </div>

                   <div style=" margin-top: 16px">
                       <Button type="primary" v-if="$route.path === '/edit'" @click="onSave"                    ></Button>
                       <Button type="primary" v-if="$route.path === '/usertheme'" @click="onToEditor"           ></Button>
                       <Button type="primary" v-if="getStatusEdit() && theme.status === 0" @click="onToBuild"   ></Button>
                       <Button type="primary" v-if="getStatusEdit()" @click="onToDownload"                      ></Button>
                       <Button type="primary" v-if="!getStatusEdit() || $route.path === '/usertheme'"  @click="onUseTemplate"></Button>
                   </div>

                </div>
            </div>
            </Col>
        </Row>
    </Modal>
</template>

<script>
    import qrcode from '@xkeshi/vue-qrcode';
    import * as config from '@/api/config'
    export default {
        name: "preview-modal",
        components: {
            qrcode,
        },
        props: {
            value: { type: Boolean, default: false },
            theme: { type: Object, default: {
                    title: '',
                    description: '',
                    themebg: ''
                }},
            title: { type: String, default: "title" },
            des: { type: String, default: 'descr', },
            qurl: { type: String, default: '', }
        },
        data(){
            return{
                inputTile: this.title,
                inputDes: this.des,
                qrcodeAdderss: this.qurl,
                modal_preView: this.preview,
                baseHost: config.caryHost + '/',
                uploadUrl: config.caryHost + 'api/edit/files/uploads?userid=',
                isImgModal: false,
            }
        },
        watch: {
            value(val){
                this.modal_preView = val;
            },

        },
        computed:{
            userinfo(){
                let info = this.$store.state.user.userinfo
                let ob = {}
                if(info){
                    ob = info
                }else {
                    ob = {
                        _id: ''
                    }
                }
                return ob
            },

            getThemebg(){
                let bg = this.theme.themebg
                if(bg === 'upload/all/img_default.png'){
                    return ''
                }
                return bg || ''
            }

        },
        created() {
        },
        mounted() {

        },
        methods: {

            //
            getStatusEdit(){
                if(this.$route.path === '/edit' || this.$route.path === '/usertheme'){
                    return true
                }else {
                    return false
                }
            },

            //
            onVisibleChange(visible){
                if(!visible){
                    this.$emit('input', visible);
                    this.qrcodeAdderss = ''
                }else {
                    this.qrcodeAdderss = this.baseHost + 'api/edit/preview/' + this.theme._id
                }
            },

            onCopyEvent(){
                let ele = document.getElementById("themeurlid");
                ele.getElementsByTagName("textarea")[0].select()
                document.execCommand("copy");
            },

            //
            handleSuccess (res, file) {
                this.theme.themebg = res.body[0].url
            },

            handleFormatError (file) {
                this.$Notice.warning({title: '', desc: ' ' + file.name + ' JPGPNG.'});
            },

            handleMaxSize (file) {
                this.$Notice.warning({title: '', desc: '  ' + file.name + ' 2M'});
            },

            onSave(){
                let pa = JSON.parse(JSON.stringify(this.theme))
                this.$store.dispatch('saveTheme', pa).then(res => {
                    // TODO 
                    this.$Message.success('')
                })
            },

            onToEditor(){
                this.$store.dispatch('pageFindOne', this.theme._id).then(() => {
                    this.$router.push({ path: '/edit', query: { itemId: this.theme._id }})
                }).catch(err => {
                    this.$Message.error('')
                })
            },

            onToBuild(){
                this.theme.status = 1
                let pa = JSON.parse(JSON.stringify(this.theme))
                this.$Spin.show();
                this.$store.dispatch('saveTheme', pa).then(res => {
                    // TODO 
                    this.$Message.success('')
                    this.$Spin.hide();
                })
            },

            onToDownload(){
                this.$Message.warning('')
                window.location.href = this.baseHost + 'api/edit/thdownload/' + this.theme._id
            },

            onUseTemplate(){
                if(this.userinfo._id){
                    let useTheme = JSON.parse(JSON.stringify(this.theme))
                    this.$store.commit('USE_CREATE_THEME', useTheme)
                    this.$Spin.show();
                    this.$store.dispatch('saveTheme', JSON.parse(JSON.stringify(this.$store.state.editor.editorTheme))).then((res) => {
                        // todo 
                        this.$store.dispatch('pageFindOne', this.$store.state.editor.editorTheme._id).then(() => {
                            this.$Spin.hide();
                            this.$router.push({ path: '/edit', query: { itemId: this.$store.state.editor.editorTheme._id }})
                        }).catch(err => {
                            this.$Message.error('')
                            this.$Spin.hide();
                        })
                    });
                    this.$store.dispatch('useTheme', useTheme)
                }else {
                    this.$Message.warning('')
                }
            },

            ontest(){
                console.log(this.theme.path === '/edit');
            }
        }
    }
</script>

<style scoped>
    .preview-plan {
        width:348px;
        padding: 2px;
        border: 1px solid #dddee1;
        border-radius: 4px
    }
    .preview-iframe {
        width: 342px;
        height: 582px;
        background: #fff
    }

    .setting-change-bg {
        display: none;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
    .theme-bg:hover .setting-change-bg {
        display: block;
    }

</style>



